import moment from 'moment';
export default (_self, h) => {
    return [
        h('DatePicker', {
            props: {
                placeholder: _self.obj.placeholder || (_self.obj.name ? "" : ""),
                type: (!_self.obj.format || _self.obj.format == 'yyyyMMdd') ? 'date' : 'datetime',
                format: _self.obj.format || 'yyyyMMdd',
                value: _self.obj.value
            },
            on: {
                "on-change" (arr) {
                    if (!_self.obj.name) {
                        return false;
                    }
                    _self.obj.value = arr;
                    _self.$emit('handleChangeVal', arr)
                }
            }
        })
    ]
}


export let datePickerConf = {
    // 
    type: 'datepicker',
    // 
    config: true,
    // label
    label: '',
    placeholder: '',
    // 
    inlineBlock: false,
    // 
    require: true,
    // name
    name: '',
    // 
    value: "",
    // 
    ruleError: '',
    // 
    relation: false,
    // name
    relation_name: '',
    // value
    relation_value: '',
    // 
    visibility: true,
    // 
    format: 'yyyyMMdd',
    //
    iconName: "ios-calendar-outline"
}


