export default (_self, h) => {
    return [
        h("Input", {
            props: {
                placeholder: _self.obj.placeholder || "",
                maxlength: parseInt(_self.obj.maxLength) || 20,
                value: _self.obj.value || ""
            },
            on: {
                "on-change": function(val) {
                    if (!_self.obj.name) {
                        return false;
                    }
                    _self.obj.value = event.currentTarget.value;
                    _self.$emit('handleChangeVal', val.currentTarget.value)
                }
            }
        })
    ];
};


export let inputConf = {
    // 
    type: 'input',
    // 
    config: true,
    // label
    label: '',
    placeholder: '',
    // 
    inlineBlock: false,
    // 
    require: true,
    // 
    maxLength: 20,
    // 
    items: [{ "label_value": null, "label_name": "" }],
    value: '',
    // name
    name: '',
    // 
    ruleError: '',
    // 
    relation: false,
    // name
    relation_name: '',
    // value
    relation_value: '',
    // 
    visibility: true,
    //
    iconName: "ios-create-outline"
}


