export default (_self, h) => {
    return [
        h("Input", {
            attrs: {
                type: "textarea",
                placeholder: _self.obj.placeholder || "",
                value: _self.obj.value,
                maxlength: _self.obj.maxLength || 200
            },
            props: {
                rows: _self.obj.maxRows || 5
            },
            on: {
                "on-change" (event) {
                    if (!_self.obj.name) {
                        return false;
                    }
                    _self.obj.value = event.currentTarget.value;
                    _self.$emit('handleChangeVal', event.currentTarget.value)
                }
            }
        })
    ];
};

export let textConf = {
    // 
    type: 'textarea',
    // 
    config: true,
    // label
    label: '',
    placeholder: '',
    // 
    inlineBlock: false,
    // 
    maxLength: 200,
    // 
    require: true,
    //  
    maxRows: 5,
    // 
    value: "",
    // name
    name: '',
    // 
    ruleError: '',
    // 
    relation: false,
    // name
    relation_name: '',
    // value
    relation_value: '',
    // 
    visibility: true,
    //
    iconName: "ios-list-box-outline"
}


