export default (_self, h) => {
    console.log("return a row")
    return [
        h("draggable",
            /*data:*/
            {
                data: {
                    sortableItem: []
                },
                props: {
                    option: {
                        forceFallback: true,
                        animation: 0,
                        ghostClass: "ghost",
                        group: {
                            name: "shared",
                            put: ["shared"],
                            //revertClone: false
                        },
                        sort: false
                    },
                    sortableItem: _self.obj.sortableItem
                },
                on: {
                    "on-change": function(val) {
                        if (!_self.obj.name) {
                            return false;
                        }
                        _self.obj.value = event.currentTarget.value;
                        _self.$emit('handleChangeVal', val.currentTarget.value)
                    }
                },
            },
            /*children*/
            [
                h(
                    "Row", {
                        gutter: _self.obj.gutter
                    }, []
                )
            ]
        )
    ];
};


export let rowConf = {
    // 
    gutter: 10,
    // 
    type: 'row',
    // 
    config: true,
    // label
    label: '',
    placeholder: '',
    // 
    inlineBlock: false,
    // 
    require: false,
    // 
    maxLength: 20,
    // 
    items: [{ "label_value": null, "label_name": "" }],
    value: '',
    // name
    name: '',
    // 
    ruleError: '',
    // 
    relation: false,
    // name
    relation_name: '',
    // value
    relation_value: '',
    // 
    visibility: true,
    //
    iconName: "ios-create-outline",
    // 
    sortableItem: []
}


