const temp_data = (size, parentIndex, parentName) => {
    if (parentIndex >= 3) {
        return [];
    }
    return Array.apply(null, { length: size })
        .map((k, v) => {
            const name = parentName + (v + 1) + "-";
            return {
                value: name.substring(0, name.length - 1),
                label: name.substring(0, name.length - 1),
                children: temp_data(size, parentIndex + 1, name)
            }
        });
}

export default (_self, h) => {
    return [
        h("cascaderMulti", {
            props: {
                placeholder: _self.obj.placeholder || "",
                data: temp_data(3, 0, ''),
                multiple: _self.obj.multiple || false,
                separate: _self.obj.multiple ? "" : "/",
                filterable: true,
                value: _self.obj.value || []
            },
            on: {
                "on-change" (arr) {
                    if (!_self.obj.name) {
                        return false;
                    }
                    _self.obj.value = arr;
                    _self.$emit('handleChangeVal', arr)
                }
            }
        })
    ];
};

export let cascaderConf = {
    // 
    type: 'cascader',
    // 
    config: true,
    // label
    label: '',
    placeholder: '',
    // 
    inlineBlock: false,
    // 
    require: true,
    // 
    multiple: false,
    // name
    name: '',
    // 
    value: [],
    // 
    ruleError: '',
    // 
    relation: false,
    // name
    relation_name: '',
    // value
    relation_value: '',
    // 
    visibility: true,
    //
    iconName: "ios-more-outline"
}


