import area from '../config/area'
export default (_self, h) => {
    let control = [
        h('Cascader', {
            class: {
                'ivu-input-wrapper ': !_self.obj.details_address
            },
            style: {
                width: _self.obj.details_address ? '200px' : '100%',
                display: 'inline-block'
            },
            props: {
                placeholder: _self.obj.placeholder || (_self.obj.name ? "" : ""),
                data: area,
                value: _self.obj.value || [],
                filterable: false,
                'change-on-select': true,
                // trigger: "hover"
            },
            on: {
                "on-change" (arr) {
                    if (!_self.obj.name) {
                        return false;
                    }
                    _self.obj.value = arr;
                    _self.$emit('handleChangeVal', arr);
                }
            }
        }),
        h("Input", {
            props: {
                placeholder: _self.obj.placeholder || "",
                ref: 'details_address',
                value: (_self.obj.value[3] || {})
                    .name
            },
            style: {
                width: 'auto',
                display: !_self.obj.name ? 'none' : 'inline-block',
                'margin-left': '5px',
                'min-width': '300px'
            },
            on: {
                "on-change": function(val) {
                    if (!_self.obj.name) {
                        return false;
                    }
                    let temp_data = _self.obj.value.slice(0, 3);
                    _self.obj.value = temp_data.concat(val.currentTarget.value);
                    _self.$emit('handleChangeVal', _self.obj.value)
                }
            }
        })
    ];

    if (!_self.obj.details_address) {
        control.splice(1, 1);
    }
    return control;
}
export let addressConf = {
    // 
    type: 'address',
    // 
    config: true,
    // label
    label: '',
    placeholder: '',
    // 
    inlineBlock: false,
    // 
    require: true,
    // 
    multiple: false,
    // name
    name: '',
    // 
    value: [],
    // 
    ruleError: '',
    // 
    relation: false,
    // name
    relation_name: '',
    // value
    relation_value: '',
    // 
    visibility: true,
    // 
    details_address: true,
    //
    iconName: "md-locate"
}


