export default (_self, h) => {
    return [
        h("CheckboxGroup", {
            props: {
                value: _self.obj.value || []
            },
            on: {
                'on-change' (arr) {
                    if (!_self.obj.name) {
                        return false;
                    }
                    _self.obj.value = arr;
                    _self.$emit('handleChangeVal', arr)
                }
            }
        }, _self.obj.items.map(v => {
            return h("Checkbox", {
                props: {
                    label: v.label_value
                }
            }, v.label_name)
        }))
    ];
};

export let checkBoxConf = {
    // 
    type: 'checkbox',
    // 
    config: true,
    // label
    label: '',
    // 
    inlineBlock: false,
    // 
    require: true,
    // 
    value: [],
    // 
    items: [{ "label_value": "1", "label_name": "1" }, { "label_value": "2", "label_name": "2" }],
    // name
    name: '',
    // 
    ruleError: '',
    // 
    relation: false,
    // name
    relation_name: '',
    // value
    relation_value: '',
    // 
    visibility: true,
    //
    iconName: "ios-checkbox-outline"
}


