export default (_self, h) => {
    return [
        h('h' + (_self.obj.level || 3), {
            style: {
                'margin-bottom': _self.obj.marginBottom + 'px',
                'margin-top': _self.obj.marginTop + 'px',
            },
            domProps: {
                innerHTML: _self.obj.label || "Title"
            }
        })
    ]
}

export let titleConf = {
    // 
    config: true,
    // 
    label: '',
    // h1-6
    level: 3,
    hasLine: true,
    marginTop: 0,
    marginBottom: 24,
    //
    iconName: "md-locate"
}


