export default (_self, h) => {
    return [
        h('uploadCustom', {
            props: {
                multiple: _self.obj.multiple || false,
                type: 'drag', //
                action: _self.obj.action || "/imageupload",
                'max-size': _self.obj.maxSize || 2048,
                defaultList: _self.obj.value,
                name: 'photo'
            },
            on: {
                handleUploadsValue(arr) {
                    if (!_self.obj.name) {
                        return false;
                    }
                    _self.obj.value = arr;
                    _self.$emit('handleChangeVal', arr)
                }
            }
        })
    ]
}


export const uploadsConf = {
    // 
    type: 'uploads',
    // 
    config: true,
    // 
    action: 'http://workflow.test/imageupload',
    // 
    require: true,
    // label
    label: '',
    // 
    maxSize: 2048,
    // 
    value: [],
    // name
    name: '',
    // 
    ruleError: '',
    // 
    relation: false,
    // name
    relation_name: '',
    // value
    relation_value: '',
    // 
    visibility: true,
    //
    iconName: "ios-attach"
}


