export default (_self, h) => {
    return [
        h("RadioGroup", {
            props: {
                value: _self.obj.value || "-1"
            },
            on: {
                'on-change' (value) {
                    if (!_self.obj.name) {
                        return false;
                    }
                    _self.obj = Object.assign(_self.obj, {
                        value
                    });
                    _self.$emit('handleChangeVal', value)
                }
            }
        }, _self.obj.items.map(v => {
            return h("Radio", {
                props: {
                    label: v.label_value
                }
            }, v.label_name)
        }))
    ];
};

export let radioConf = {
    // 
    type: 'radio',
    // 
    config: true,
    // label
    label: '',
    // 
    inlineBlock: false,
    // 
    require: true,
    // 
    value: '',
    // 
    items: [{ "label_value": "1", "label_name": "1" }, { "label_value": "2", "label_name": "2" }],
    // name
    name: '',
    // 
    ruleError: '',
    // 
    relation: false,
    // name
    relation_name: '',
    // value
    relation_value: '',
    // 
    visibility: true,
    //
    iconName: "ios-radio-button-on"
}


