<template>
  <div class="container">
    <Row :gutter="10">
      <Col span="6" class="sortable_container">
        <Card>
          <p slot="title">
            <Icon type="ios-apps-outline"></Icon>
            {{ $t("components") }}
        </p>
          <draggable :clone="cloneData" :list="form_list" :options="dragOptions1">
            <!--transition-group class="form-list-group" type="transition" :name="'flip-list'" tag="div"-->
              <renders
              v-for="(element,index) in form_list"
              :key="11+index"
              :ele="element.ele"
              :obj="element.obj || {}"
              onlyIcon
              ></renders>
            <!--/transition-group-->
          </draggable>
        </Card>
      </Col>
      <Col span="18" class="sortable_item">
        <Card>
        <Modal
        :value=true
        :mask=false
        :width=74.7
        :styles="{
          marginLeft:'25.3%',
          marginTop:'10px'
        }"
        :transfer=false
        >
          <Form ref="formValidate" class="b-a" :label-width="100" :model="formData" @submit.native.prevent>
            <Alert style="margin: 15px 15px 0;" type="warning" show-icon></Alert>
            <draggable :list="sortable_item" :options="dragOptions2">
              <transition-group class="form-list-group" type="transition" :name="'flip-list'" tag="div">
                <renders @handleRemoveEle="removeEle" @handleConfEle="confEle" @changeVisibility="changeVisibility" v-for="(element,index) in sortable_item" :key="12+index" :index="index" :ele="element.ele" :obj="element.obj || {}" :data="formData" @handleChangeVal="val => handleChangeVal(val,element)" :sortableItem="sortable_item" :config-icon="true">
                </renders>
              </transition-group>
            </draggable>
          </Form>
          <div slot="footer">
              <Button type="primary" @click="handleSubmit()">{{$t("preview")}}</Button>
              <Button type="warning" @click="handleReset()" style="margin-left: 8px">{{$t("reset")}}</Button>
          </div>
        </Modal>
        </Card>
      </Col>
      <Modal v-model="showModal" :title="'' + modalFormData.modalTitle + ''" :mask-closable="false">
        <Form class="form_content" :label-width="80" :model="modalFormData" ref="modalFormData">
          <FormItem label="" v-if="typeof modalFormData.label != 'undefined'">
            <Input v-model="modalFormData.label" placeholder="" :maxlength="4"></Input>
          </FormItem>
          <FormItem label="" v-if="showModal">
            <Select v-model="modalFormData.dict" filterable @on-change="handleDataDictChange">
              <!-- valuejsonparent_namehandleDataDictChangemodalFormData -->
              <Option :disabled="dataDictSelected.indexOf(item.id) >= 0" v-for="item in dataDictList" :value="JSON.stringify({
                id: item.id, parent_name: item.parent_name})" :key="item.id">{{ item.label }}</Option>
            </Select>
          </FormItem>
          <FormItem label="name" v-if="typeof modalFormData.name != 'undefined'">
            <Input v-model="modalFormData.name" placeholder="" disabled></Input>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.relation != 'undefined'">
            <!-- namerelationList -->
            <Checkbox :disabled="!modalFormData.name || !relationList.length" v-model="modalFormData.relation"></Checkbox>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.relation != 'undefined' && modalFormData.relation">
            <Select v-model="modalFormData.relation_name" class="inline-block" style="width: 150px" @on-change="_=>modalFormData.relation_value = ''">
              <Option :disabled="item.obj.name == modalFormData.name" v-for="(item,index) in relationList" :key="index" :value="item.obj.name">{{item.obj.label}}</Option>
            </Select>
            <p class="inline-block padder-sm"></p>
            <Select v-model="modalFormData.relation_value" class="inline-block" style="width: 150px">
              <Option v-for="(item,index) in relationValue" :key="index" :value="item.label_value">{{item.label_name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="placeholder" v-if="typeof modalFormData.placeholder != 'undefined'">
            <Input v-model="modalFormData.placeholder" placeholder="placeholder"></Input>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.maxLength != 'undefined'">
            <InputNumber v-model="modalFormData.maxLength" placeholder="">
            </InputNumber>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.maxSize != 'undefined'">
            <InputNumber :formatter="value => `${value}kb`" :parser="value => value.replace('kb', '')" v-model="modalFormData.maxSize" placeholder="">
            </InputNumber>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.marginTop != 'undefined'">
            <InputNumber :formatter="value => `${value}px`" :parser="value => value.replace('px', '')" v-model="modalFormData.marginTop" placeholder="">
            </InputNumber>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.marginBottom != 'undefined'">
            <InputNumber :formatter="value => `${value}px`" :parser="value => value.replace('px', '')" v-model="modalFormData.marginBottom" placeholder="">
            </InputNumber>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.details_address != 'undefined'">
            <Checkbox v-model="modalFormData.details_address"></Checkbox>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.require != 'undefined'">
            <Checkbox v-model="modalFormData.require"></Checkbox>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.ruleError != 'undefined'">
            <Input v-model="modalFormData.ruleError" placeholder=""></Input>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.multiple != 'undefined' && modalFormData.type != 'address'">
            <Checkbox v-model="modalFormData.multiple"></Checkbox>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.format != 'undefined'">
            <RadioGroup v-model="modalFormData.format">
              <Radio label="yyyyMMdd"></Radio>
              <Radio label="yyyy-MM-dd HH:mm"></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.inlineBlock != 'undefined'">
            <Checkbox v-model="modalFormData.inlineBlock"></Checkbox>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.maxRows != 'undefined'">
            <Slider v-model="modalFormData.maxRows" :min="2" :max="10"></Slider>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.level != 'undefined'">
            <InputNumber :max="6" :min="1" v-model="modalFormData.level"></InputNumber>
          </FormItem>
          <FormItem label="" v-if="typeof modalFormData.color != 'undefined'">
            <ColorPicker v-model="modalFormData.color" />
          </FormItem>
        </Form>
        <div slot="footer">
          <Button type="text" @click="handleCancel"></Button>
          <Button type="primary" :loading="modalFormData.loading" @click="handleOk"></Button>
        </div>
      </Modal>
    </Row>
  </div>
</template>
<script>
import draggable from "vuedraggable";
import form_list from "./custom_form/FormList";
export default {
  components: {
    draggable
  },
  data() {
    return {
      form_list: form_list,
      sortable_item: [],
      showModal: false,
      // 
      // bugcolor      
      modalFormData: {
        color: '',
        loading: false
      },
      formData: {},
      dataDict: []
    };
  },
  methods: {
    // 
    handleSubmit() {
      localStorage.setItem('template_form', JSON.stringify(this.sortable_item.filter(v => {
        return !!v.obj.name
      })));
      this.$router.push('/render');
    },
    // 
    handleReset() {
      this.sortable_item = [];
    },
    // modal
    handleDataDictChange(val) {
      // valmodalFormData.dict
      const obj = JSON.parse(val);
      // modal_submit
      this.$set(this.modalFormData, 'loading', true);
      this.$http.get(`/static/label.${obj.id}.json`).then(d => {
        this.modalFormData = Object.assign({}, this.modalFormData, {
          name: d.data.name,
          loading: false,
          items: d.data.items,
          parent_name: obj.parent_name
        });
      });
    },
    // 
    handleChangeVal(val, element) {
      this.$set(this.formData, element.obj.name, val);
    },
    // https://github.com/SortableJS/Vue.Draggable#clone
    // ,
    cloneData(original) {
      // modal
      original.obj.modalTitle = original.obj.label || "";
      // 
      return JSON.parse(JSON.stringify(original));
    },
    // modal
    handleOk() {
      const index = this.modalFormData.listIndex;
      this.sortable_item[index].obj = Object.assign({},
        this.sortable_item[index].obj,
        this.modalFormData
      );
      this.handleCancel();
    },
    // modalmodal
    handleCancel() {
      this.showModal = false;
      setTimeout(_ => {
        this.modalFormData = {
          color: '',
          loading: false
        };
      }, 500)
    },
    // modal,
    confEle(index) {
      const list_temp = Object.assign({}, this.sortable_item[index]);
      for (let i in list_temp.obj) {
        this.modalFormData[i] = list_temp.obj[i];
      }
      // colormodalFormDatacolor
      if (!list_temp.obj['color']) delete this.modalFormData.color;
      // index
      this.modalFormData.listIndex = index;
      // Vue 
      this.modalFormData = Object.assign({}, this.modalFormData);
      this.showModal = true;
    },
    // 
    removeEle(index) {
      let name = this.sortable_item[index].obj.name;
      this.sortable_item.splice(index, 1);
      if (!name) return;
      for (let i in this.sortable_item) {
        // relationtrue
        if (this.sortable_item[i].obj.relation && this.sortable_item[i].obj.relation_name === name) {
          this.$set(this.sortable_item[i].obj, "relation", false);
          this.$set(this.sortable_item[i].obj, "relation_name", "");
          this.$set(this.sortable_item[i].obj, "relation_value", "");
          break;
        }
      }
    },
    // 
    changeVisibility(index, visibility) {
      this.$set(this.sortable_item[index].obj, 'visibility', visibility);
    }
  },
  watch: {
    showModal(val) {
      if (!val) {
        this.handleCancel();
      }
    }
  },
  computed: {
    // 
    dataDictSelected() {
      return this.sortable_item.map(v => {
        const obj = JSON.parse(v.obj.dict || '{}');
        return obj.id || -1;
      })
    },
    // 
    dataDictList() {
      return this.dataDict.filter(v => {
        return v.type == this.modalFormData.type;
      })
    },
    // 1
    dragOptions1() {
      return {
        animation: 0,
        ghostClass: "ghost",
        // 
        group: {
          name: "shared",
          pull: "clone",
          revertClone: false
        },
        // 
        sort: false
      };
    },
    // 2
    dragOptions2() {
      return {
        animation: 0,
        ghostClass: "ghost",
        group: {
          // shared,pull
          put: ["shared"]
        }
      };
    },
    // 
    relationList() {
      // type
      let type = ['select', 'radio', 'checkbox'];
      const arr = this.sortable_item.filter(k => {
        return type.indexOf(k.ele) >= 0 && !!k.obj.name;
      })
      return arr;
    },
    // 
    relationValue() {
      const name = this.modalFormData.relation_name;
      let items = [];
      if (!name) return items;
      for (let i in this.sortable_item) {
        if (this.sortable_item[i].obj.name == name) {
          items = this.sortable_item[i].obj.items;
        }
      }
      return items;
    }
  },
  created() {
    // /static/label.json
    this.$http.get('/static/label.json').then(d => {
      this.dataDict = d.data.items;
    });
    this.sortable_item = JSON.parse(localStorage.getItem('template_form') || '[]');

    //1ID
    //2ID
    //3
  }
};
</script>
<style>
.inline {
  display: inline-block;
}

.m-l-lg {
  margin-left: 30px
}

.wrapper {
  padding: 15px
}

.inline-block {
  display: inline-block;
}

.padder-sm {
  padding-right: 10px;
  padding-left: 10px
}

.b-a {
  border: 1px solid #ccc;
}

.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}

.form-list-group {
  min-height: 200px;
  padding: 20px !important;
}

/* items move */

.items,
.items * {
  cursor: move;
}

/*  */

/* P Hr Title */

.items .item-icon {
  transition: all 2s ease;
  position: absolute;
  top: -18px;
  right: 0px;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
}

/* form */

.ivu-form-item.items .item-icon {
  top: -25px;
}

/*  */

.item-icon i {
  cursor: pointer !important;
  margin-right: 5px;
}

.items:hover .item-icon {
  transition: inherit;
  opacity: 1;
  max-height: 50px;
}

/*  margin-bottom */

.form_content .ivu-form-item:last-child {
  margin-bottom: 0;
}


/*  */

.ivu-form-item-required .ivu-form-item-label:before {
  content: '';
}

.items.sortable-items-required .ivu-form-item-label:before {
  content: '*';
  display: inline-block;
  margin-right: 4px;
  line-height: 1;
  font-family: SimSun;
  font-size: 12px;
  color: #ed3f14;
}
</style>


